<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
			>
			<el-form-item class="add-item" label="运动名称" prop="yundongmingcheng">
				<el-input v-model="ruleForm.yundongmingcheng" 
					placeholder="运动名称" clearable :disabled=" false  ||ro.yundongmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="运动类型" prop="yundongleixing">
				<el-input v-model="ruleForm.yundongleixing" 
					placeholder="运动类型" clearable :disabled=" false  ||ro.yundongleixing"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="图片" v-if="type!='cross' || (type=='cross' && !ro.tupian)" prop="tupian">
				<file-upload
					tip="点击上传图片"
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.tupian?ruleForm.tupian:''"
					@change="tupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="图片" prop="tupian">
				<img v-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="视频" v-if="type!='cross' || (type=='cross' && !ro.shipin)" prop="shipin">
				<file-upload
					tip="点击上传视频"
					action="file/upload"
					:limit="1"
					:type="2"
					:multiple="true"
					:fileUrls="ruleForm.shipin?ruleForm.shipin:''"
					@change="shipinUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="视频" prop="shipin">
				<div class="viewBtn" v-if="ruleForm.shipin" @click="download(baseUrl + ruleForm.shipin)" type="success">预览</div>
				<div class="unviewBtn" v-else disabled>暂无</div>
			</el-form-item>
			<el-form-item class="add-item" label="运动强度" prop="yundongqiangdu">
				<el-input v-model="ruleForm.yundongqiangdu" 
					placeholder="运动强度" clearable :disabled=" false  ||ro.yundongqiangdu"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="运动周期" prop="yundongzhouqi">
				<el-input v-model="ruleForm.yundongzhouqi" 
					placeholder="运动周期" clearable :disabled=" false  ||ro.yundongzhouqi"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="运动备注" prop="yundongbeizhu">
				<el-input v-model="ruleForm.yundongbeizhu" 
					placeholder="运动备注" clearable :disabled=" false  ||ro.yundongbeizhu"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="运动目标" prop="yundongmubiao">
				<el-input v-model="ruleForm.yundongmubiao" 
					placeholder="运动目标" clearable :disabled=" false  ||ro.yundongmubiao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="用户账号" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="用户账号" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="用户姓名" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="用户姓名" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text">提交</span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text">取消</span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					yundongmingcheng : false,
					yundongleixing : false,
					tupian : false,
					shipin : false,
					yundongqiangdu : false,
					yundongzhouqi : false,
					yundongbeizhu : false,
					yundongmubiao : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					yundongmingcheng: '',
					yundongleixing: '',
					tupian: '',
					shipin: '',
					yundongqiangdu: '',
					yundongzhouqi: '',
					yundongbeizhu: '',
					yundongmubiao: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
				},


				rules: {
					yundongmingcheng: [
						{ required: true, message: '运动名称不能为空', trigger: 'blur' },
					],
					yundongleixing: [
						{ required: true, message: '运动类型不能为空', trigger: 'blur' },
					],
					tupian: [
					],
					shipin: [
					],
					yundongqiangdu: [
					],
					yundongzhouqi: [
					],
					yundongbeizhu: [
					],
					yundongmubiao: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='yundongmingcheng'){
							this.ruleForm.yundongmingcheng = obj[o];
							this.ro.yundongmingcheng = true;
							continue;
						}
						if(o=='yundongleixing'){
							this.ruleForm.yundongleixing = obj[o];
							this.ro.yundongleixing = true;
							continue;
						}
						if(o=='tupian'){
							this.ruleForm.tupian = obj[o].split(",")[0];
							this.ro.tupian = true;
							continue;
						}
						if(o=='shipin'){
							this.ruleForm.shipin = obj[o];
							this.ro.shipin = true;
							continue;
						}
						if(o=='yundongqiangdu'){
							this.ruleForm.yundongqiangdu = obj[o];
							this.ro.yundongqiangdu = true;
							continue;
						}
						if(o=='yundongzhouqi'){
							this.ruleForm.yundongzhouqi = obj[o];
							this.ro.yundongzhouqi = true;
							continue;
						}
						if(o=='yundongbeizhu'){
							this.ruleForm.yundongbeizhu = obj[o];
							this.ro.yundongbeizhu = true;
							continue;
						}
						if(o=='yundongmubiao'){
							this.ruleForm.yundongmubiao = obj[o];
							this.ro.yundongmubiao = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 获取用户信息
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 多级联动参数
			// 多级联动参数
			info() {
				this.$http.get(`yundongjihua/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 提交
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`yundongjihua/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.$router.go(-1);
			},
			tupianUploadChange(fileUrls) {
				this.ruleForm.tupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			shipinUploadChange(fileUrls) {
				this.ruleForm.shipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

