<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
			>
			<el-form-item class="add-item" label="食物名称" prop="shiwumingcheng">
				<el-input v-model="ruleForm.shiwumingcheng" 
					placeholder="食物名称" clearable :disabled=" false  ||ro.shiwumingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="封面" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip="点击上传封面"
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="封面" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="蛋白质" prop="danbaizhi">
				<el-input-number v-model="ruleForm.danbaizhi" placeholder="蛋白质" :disabled=" false ||ro.danbaizhi"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="碳水化合物" prop="tanshuihuahewu">
				<el-input-number v-model="ruleForm.tanshuihuahewu" placeholder="碳水化合物" :disabled=" false ||ro.tanshuihuahewu"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="脂肪" prop="zhifang">
				<el-input-number v-model="ruleForm.zhifang" placeholder="脂肪" :disabled=" false ||ro.zhifang"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="维生素" prop="weishengsu">
				<el-input-number v-model="ruleForm.weishengsu" placeholder="维生素" :disabled=" false ||ro.weishengsu"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="分析时间" prop="fenxishijian">
				<el-date-picker
					:disabled="true  ||ro.fenxishijian"
					format="yyyy 年 MM 月 dd 日"
					value-format="yyyy-MM-dd"
					v-model="ruleForm.fenxishijian" 
					type="date"
					placeholder="分析时间">
				</el-date-picker> 
			</el-form-item>
			<el-form-item class="add-item" label="营养分析" prop="yingyangfenxi">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="营养分析"
					v-model="ruleForm.yingyangfenxi">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="营养建议" prop="yingyangjianyi">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="营养建议"
					v-model="ruleForm.yingyangjianyi">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text">提交</span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text">取消</span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					shiwumingcheng : false,
					fengmian : false,
					danbaizhi : false,
					tanshuihuahewu : false,
					zhifang : false,
					weishengsu : false,
					yingyangfenxi : false,
					yingyangjianyi : false,
					fenxishijian : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					discussnum : false,
					totalscore : false,
					storeupnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					shiwumingcheng: '',
					fengmian: '',
					danbaizhi: '',
					tanshuihuahewu: '',
					zhifang: '',
					weishengsu: '',
					yingyangfenxi: '',
					yingyangjianyi: '',
					fenxishijian: '',
					thumbsupnum: '',
					crazilynum: '',
					clicktime: '',
					discussnum: '',
					totalscore: '',
					storeupnum: '',
				},


				rules: {
					shiwumingcheng: [
					],
					fengmian: [
					],
					danbaizhi: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					tanshuihuahewu: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					zhifang: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					weishengsu: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					yingyangfenxi: [
					],
					yingyangjianyi: [
					],
					fenxishijian: [
					],
					thumbsupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					clicktime: [
					],
					discussnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					totalscore: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.fenxishijian = this.getCurDate()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='shiwumingcheng'){
							this.ruleForm.shiwumingcheng = obj[o];
							this.ro.shiwumingcheng = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='danbaizhi'){
							this.ruleForm.danbaizhi = obj[o];
							this.ro.danbaizhi = true;
							continue;
						}
						if(o=='tanshuihuahewu'){
							this.ruleForm.tanshuihuahewu = obj[o];
							this.ro.tanshuihuahewu = true;
							continue;
						}
						if(o=='zhifang'){
							this.ruleForm.zhifang = obj[o];
							this.ro.zhifang = true;
							continue;
						}
						if(o=='weishengsu'){
							this.ruleForm.weishengsu = obj[o];
							this.ro.weishengsu = true;
							continue;
						}
						if(o=='yingyangfenxi'){
							this.ruleForm.yingyangfenxi = obj[o];
							this.ro.yingyangfenxi = true;
							continue;
						}
						if(o=='yingyangjianyi'){
							this.ruleForm.yingyangjianyi = obj[o];
							this.ro.yingyangjianyi = true;
							continue;
						}
						if(o=='fenxishijian'){
							this.ruleForm.fenxishijian = obj[o];
							this.ro.fenxishijian = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 获取用户信息
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 多级联动参数
			// 多级联动参数
			info() {
				this.$http.get(`shujufenxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 提交
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`shujufenxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

