<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
			>
			<el-form-item class="add-item" label="计划名称" prop="jihuamingcheng">
				<el-input v-model="ruleForm.jihuamingcheng" 
					placeholder="计划名称" clearable :disabled=" false  ||ro.jihuamingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="封面" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip="点击上传封面"
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="封面" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="计划日期" prop="jihuariqi">
				<el-date-picker
					:disabled=" false  ||ro.jihuariqi"
					format="yyyy 年 MM 月 dd 日"
					value-format="yyyy-MM-dd"
					v-model="ruleForm.jihuariqi" 
					type="date"
					:picker-options="jihuariqiPickerOptions"
					placeholder="计划日期">
				</el-date-picker> 
			</el-form-item>
			<el-form-item class="add-item" label="用户账号" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="用户账号" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="用户姓名" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="用户姓名" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="早餐搭配" prop="zaocandapei">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="早餐搭配"
					v-model="ruleForm.zaocandapei">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="午餐搭配" prop="wucandapei">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="午餐搭配"
					v-model="ruleForm.wucandapei">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="晚餐搭配" prop="wancandapei">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="晚餐搭配"
					v-model="ruleForm.wancandapei">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="加餐搭配" prop="jiacandapei">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="加餐搭配"
					v-model="ruleForm.jiacandapei">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="计划目标" prop="jihuamubiao">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="计划目标"
					v-model="ruleForm.jihuamubiao">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="计划详情" prop="jihuaxiangqing">
				<editor 
					v-model="ruleForm.jihuaxiangqing" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text">提交</span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text">取消</span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					jihuamingcheng : false,
					fengmian : false,
					zaocandapei : false,
					wucandapei : false,
					wancandapei : false,
					jiacandapei : false,
					jihuamubiao : false,
					jihuariqi : false,
					jihuaxiangqing : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					jihuamingcheng: '',
					fengmian: '',
					zaocandapei: '',
					wucandapei: '',
					wancandapei: '',
					jiacandapei: '',
					jihuamubiao: '',
					jihuariqi: '',
					jihuaxiangqing: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
				},

				// 只能选择今天和之后日期
				jihuariqiPickerOptions: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},

				rules: {
					jihuamingcheng: [
					],
					fengmian: [
					],
					zaocandapei: [
					],
					wucandapei: [
					],
					wancandapei: [
					],
					jiacandapei: [
					],
					jihuamubiao: [
						{ required: true, message: '计划目标不能为空', trigger: 'blur' },
					],
					jihuariqi: [
					],
					jihuaxiangqing: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.jihuariqi = this.getCurDate()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='jihuamingcheng'){
							this.ruleForm.jihuamingcheng = obj[o];
							this.ro.jihuamingcheng = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='zaocandapei'){
							this.ruleForm.zaocandapei = obj[o];
							this.ro.zaocandapei = true;
							continue;
						}
						if(o=='wucandapei'){
							this.ruleForm.wucandapei = obj[o];
							this.ro.wucandapei = true;
							continue;
						}
						if(o=='wancandapei'){
							this.ruleForm.wancandapei = obj[o];
							this.ro.wancandapei = true;
							continue;
						}
						if(o=='jiacandapei'){
							this.ruleForm.jiacandapei = obj[o];
							this.ro.jiacandapei = true;
							continue;
						}
						if(o=='jihuamubiao'){
							this.ruleForm.jihuamubiao = obj[o];
							this.ro.jihuamubiao = true;
							continue;
						}
						if(o=='jihuariqi'){
							this.ruleForm.jihuariqi = obj[o];
							this.ro.jihuariqi = true;
							continue;
						}
						if(o=='jihuaxiangqing'){
							this.ruleForm.jihuaxiangqing = obj[o];
							this.ro.jihuaxiangqing = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 获取用户信息
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 多级联动参数
			// 多级联动参数
			info() {
				this.$http.get(`yinshijihua/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 提交
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`yinshijihua/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

